<template>
  <div>
    <p>当前正在进行第 {{ current + 1 }} 步</p>
    <br>
    <Steps :current="current">
      <Step title="选择会议室" icon="md-easel" content="根据最多容纳人数选择会议室"></Step>
      <Step title="选择会议时间" icon="md-clock" content="根据会议时间安排和可预定时段选择会议时间"></Step>
      <Step title="邀请参会人" icon="md-contacts" content="选择需要参加会议的人"></Step>
      <Step title="输入会议内容" icon="md-clipboard" content="输入会议的主题和内容等信息"></Step>
    </Steps>
    <br>
    <Button type="primary" @click="next">下一步</Button>
    <Calendar
      ref="Calendar"
      :markDateMore="arr"
      :markDate="arr2"
      v-on:isToday="clickToday"
      agoDayHide="1530115221"
      v-on:choseDay="clickDay"
      v-on:changeMonth="changeDate"
    ></Calendar>
  </div>
</template>

<script>
  import Calendar from 'vue-calendar-component'
  export default {
    name: "BookMeeting",
    data() {
      return {
        current: 0,
        arr2: [],
        arr: [
          {
            date: "2018/8/1",
            className: "mark1"
          },
          {
            date: "2018/8/13",
            className: "mark2"
          }
        ]
      }
    },
    components: {
      Calendar
    },
    methods: {
      next() {
        if (this.current === 3) {
          this.current = 0;
        } else {
          this.current += 1;
        }
      },
      clickDay(data) {
        console.log("选中了", data); //选中某天
        this.$toast("选中了" + data);
      },
      clickToday(data) {
        console.log("跳到了本月今天", data); //跳到了本月
      },
      changeDate(data) {
        this.$toast("切换到的月份为" + data);
        console.log("左右点击切换月份", data); //左右点击切换月份
      },
      demo() {
        this.$refs.Calendar.ChoseMonth("2018-12-13"); //跳到12月12日选中12月12日
      }
    },
    created() {
      function format(date, index) {
        date = new Date(date);
        return `${date.getFullYear()}-${date.getMonth() + 1}-${index}`;
      }
      setTimeout(() => {
        this.arr = [
          {
            date: format(new Date(), 3),
            className: "mark1"
          },
          {
            date: format(new Date(), 12),
            className: "mark2"
          }
        ];
        this.arr.push({
          date: format(new Date(), 15),
          className: "mark1"
        });
      }, 300);
    }
  }
</script>

<style scoped>

</style>



